.searchbox {
	display: -ms-flexbox;
	display: flex;
	height: 50px;
	background-color: #265B6C;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.searchbox-input {
	margin: 3px;
	padding: 5px;
}

.searchbox-button {
	margin: 3px;

	background-color: #AC7438;
	color: white;
	-webkit-box-shadow: 2px 2px 5px 0 rgba(0,0,0,.75);
	        box-shadow: 2px 2px 5px 0 rgba(0,0,0,.75);
	text-decoration: none;
	height: 32px;
	line-height: 32px;
	text-align: center;
	border-radius: 50px;
	width: 100px;
}



